body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-outfit: Outfit;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-7xl: 26px;
  --font-size-xl: 20px;
  --font-size-17xl: 36px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-maroon: #66000d;
  --color-white: #fff;
  --color-gray: #350107;

  /* Border radiuses */
  --br-mini: 15px;
}
